<template>
    <div class="app">
        <div v-html="content"></div>
    </div>
</template>

<script>
    import { redstrategy } from '@/api/red'
    export default {
        name: "Redshare",
        data()
        {
            return {

                content:''
            }
        },


        created()
        {


        },
        mounted()
        {
            this.getContent()
        },
        methods: {
            // 获取邀请信息
            getContent() {
                redstrategy().then(res => {
                    this.content = res
                })
            },
        }
    }
</script>

<style scoped>
    .app div p {
        font-size: 0.13rem;
        color: #333333;
    }
    .app div img {
        width: 3rem;
        padding: .1rem 0;
        display: inline-block;
        border-radius: .04rem;
        margin: auto;
    }
</style>



